let changelook = ["所有人可见", "仅自己可见"];

import uniNavBar from "../../components/uni-nav-bar/uni-nav-bar.vue"
import uploadImage from "../../components/common/upload-image.vue"
import uniPopup from "../../components/uni-popup/uni-popup.vue"

export default {
	components: {
		uniNavBar,
		uploadImage,
		uniPopup,
	},
	data() {
		return {
			showpopup: false,
			yinsi: "所有人可见",
			text:"",
			imgList: [],
			type: 'center',
			content: '弹 popup',
			isget: false,
		}
	},
	onBackPress() {
		console.log("onBackPress is pressed!")
		// 判断用户是否提交了信息：验证 text 和 imgList
		if(this.text || this.imgList.length>0) {
			if(!this.isget) {
				this.baocun();
				return true;
			}	
		}			
	},
	methods: {
		// 保存为草稿
		baocun() {
			uni.showModal({
				content:"是否要保存为草稿?", 
				cancelText:"不保存",
				confirmText:"保存",
				success: res => {
					if(res.confirm) {
						console.log("保存");
					} else {
						console.log("不保存");
					}
					this.isget = true;
					uni.navigateBack({
						delta: 1
					})
				}
			});
		},
		// 返回
		back() {
			uni.navigateBack({
				delta: 1
			})
		},
		// 发布
		submit() {
			console.log("发布")
		},
		// 隐私
		changelook() {
			console.log("隐私")
			uni.showActionSheet({ 
				itemList: changelook, // "itemList": 按钮的文字数组
				success: (res) => {
					console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
					this.yinsi = changelook[res.tapIndex]
				},
			});
		},
		onUpload(arr) {
			this.imgList = arr;
			console.log(this.imgList)
		},
		// hidePopup() {
		// 	this.showpopup = false;
		// },
		togglePopup(type, open) {
			console.log('togglePopup() is called! ---');
			this.content = '居中弹出 popup'
			this.type = type
			this['show' + open] = true
		},
		change(e) {
			console.log('是否打开:' + e.show)
			if (!e.show) {
				this.showpopup = false
			}
		},
	}
}
